body {
  text-align: center;
  background-color: #e8fcff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  text-align: left;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
  transition: border-color .2s ease-in-out;
}

.breed-select:focus, .breed-select:hover {
  border-color: #007bff;
}

.loader, .error {
  color: tomato;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 600;
}

.cat-info {
  gap: 30px;
  margin-top: 20px;
  display: flex;
}

.textInfo {
  max-width: 800px;
  font-size: large;
}

.is-hidden {
  display: none;
}

